import React from 'react';
import Lottie from 'react-lottie';
import * as animationData from '../assets/house.json';
import Header from '../components/header';

import '../App.css';

class Home extends React.PureComponent {
    constructor(props) {
        super(props);
    }
    
    render () {
        const defaultOptions = {
            loop: true,
            autoplay: true, 
            animationData: animationData.default,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            }
        };

          
        return (
            <>
                <section>
                    <div className="container">
                        <Lottie 
                            options={defaultOptions}
                            height={300}
                            width={300}
                        />

                        <h4 className="hometxt">
                            Here’s how it works, you’ll answer <br /> some questions and we match you<br /> with the right aparments.
                        </h4>

                        <button id="getstarted-button">
                            Start
                        </button>
                    </div>
                </section>
            </>
        );
    }   
}

export default Home;